/** @jsx jsx */
import { jsx, css } from "@emotion/react"
import Carousel from "../../../../carousel"
import { mq } from "Common/css"
import ProductImage from "../image"

import { getMediaInfo } from "Common/images"
import ProductFeaturedImageVideo from "../video"

function ProductCarouselImages({
  defaultCarouselSettings,
  images,
  element,
  payload,
  settings,
}) {
  const ProductCarouselImagesCSS = css`
    margin-bottom: 30px;
  `

  const ProductCarouselSliderCSS = css`
    .slick-list {
      ${mq("large")} {
        width: 100%;
      }
    }

    .slick-next {
      right: 7px;
      background-image: url("data:image/svg+xml;base64,");

      &:hover {
        background-image: url("data:image/svg+xml;base64,");
      }
    }

    .slick-prev {
      left: 7px;
      z-index: 99;
      background-image: url("data:image/svg+xml;base64,");

      &:hover {
        background-image: url("data:image/svg+xml;base64,");
      }
    }

    .slick-next,
    .slick-prev {
      transition: all ease 0.2s;
      visibility: visible;
      opacity: 1;
      background-repeat: no-repeat;
      text-align: center;
      background-position: 45% 50%;
      background-size: 13px;
      padding: 70px 25px;
      top: calc(50% - 10px);

      &:hover {
        background-repeat: no-repeat;
        background-position: 45% 50%;
        background-size: 13px;
      }

      svg {
        display: none;
      }
    }
  `

  return shopwp.misc.isPro ? (
    <div css={ProductCarouselImagesCSS}>
      <Carousel
        defaultSettings={defaultCarouselSettings}
        customSettings={{
          slidesToScroll: 1,
          slidesToShow: 1,
          adaptiveHeight: true,
        }}
        extraCSS={ProductCarouselSliderCSS}
        element={element}
      >
        {images.map((image) => (
          <div
            key={
              image.node.mediaContentType === "IMAGE"
                ? image.node.image.id
                : image.node.previewImage.id
            }
          >
            {image.node.mediaContentType === "IMAGE" ? (
              <img
                key={image.node.image.id}
                src={image.node.image.originalSrc}
                alt={image.node.image.altText}
              />
            ) : (
              <ProductFeaturedImageVideo
                key={image.node.previewImage.id}
                videoData={image.node}
              />
            )}
          </div>
        ))}
      </Carousel>
    </div>
  ) : null
}

export default ProductCarouselImages
